.carousel-inner img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: contain;
    
}

.carousel-content {
    /* border: 2px solid black; */
    background-color: rgba(71, 74, 70, 0.3);
    margin-left: 5rem;
    margin-right: 5rem;
    padding-bottom: 1rem;
    padding-left: 1rem;
    
  }


.carousel-inner {
    height: 500px;
    max-width: 100%;
    position: relative;
    display: flex;
    background-size: cover;
    background-position: center;
    transition: 500ms cubic-bezier(0.48, 0.15, 0.8, 1);
    background-color: rgb(0,0,0,0.3);
    /* margin-bottom: 1.5rem; */
}

.carousel-inner .left-carousel {
    flex: 5% 1;
    height: 100%;
    background-color: rgb(0,0,0,0.3);
    display: grid;
    place-items: center;
    color: var(--primary-white);
    cursor: pointer;
    
}
.carousel-inner .center-carousel {
    flex: 80% 1;
    height: 100%;
    display: grid;
    justify-content: center;
    place-items: center;
    background-size: cover;
    background-position: center;
       
}
.carousel-inner .right-carousel {
    flex: 5% 1;
    height: 100%;
    background-color: rgb(0,0,0,0.3);
    display: grid;
    place-items: center;
    color: var(--primary-white);
    cursor: pointer;
}

.right-carousel:hover, .left-carousel:hover{
    color: var(--primary-orange)
}

/*Navigation Dots*/

.navigation-dots {
    display: flex;
    height: 32px;
    align-items: center;
    justify-content: center;
    /* margin: 16px 0; */

}

.single-dot {
    background-color: var(--primary-white);
    height: 22px;
    width: 22px;
    border: 2px solid black;
    border-radius: 50%;
    margin: 0 8px;
    cursor: pointer;
    transition: 400ms;
}

.single-dot.active {
    background-color: var(--primary-orange);
}

.portfolio-links {
  color: var(--primary-orange) !important;
  text-decoration: underline;
}

.portfolio-links:hover {
    color: var(--primary-white) !important;
}

:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;

}

.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* body {
  
} */

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*NAVBAR*/
nav{
  z-index: 2;
}

.logo {
  width: 5rem;
}

/*MENU LINKS*/
nav a.nav-link {
  font-weight: 500;
  letter-spacing: 0.05rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.2rem solid rgba(255, 255, 255, 0);
  border-top: 0.2rem solid var(--primary-dark);
}

.navbar-brand{
  max-width: 3rem;
  padding: 5 !important;
  
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-bottom: 0.2rem solid #f9ab00;
  border-bottom: 0.2rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-bottom: 0.2rem solid #f9ab00;
  border-bottom: 0.2rem solid var(--primary-orange);
}

