/*HEADER*/
.header-wrapper {
  position: relative;
  background: url(/static/media/background-code.06c664cf.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display: flex;
  position: relative;
  padding-top: 7rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info-lower {
  display: flex;
  position: relative;
  padding-top: 5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Bebas Neue", sans-serif;
  z-index: 1;

}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
}

.main-info-lower h2 {
  color: #f9ab00;
  color: var(--primary-orange);
}

.main-info h3 {
  color: #fff;
  color: var(--primary-white);
}

@media(max-width:768px){
  .main-info h1{
    font-size: 1.5rem !important;
  }
  .typed-text{
    font-size: 1.2rem !important;
  }
}

/*TYPED-TEXT*/
.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

/*HEADER MAIN BUTTON*/
.btn-main-offer {
  border: 2px solid #f9ab00;
  border: 2px solid var(--primary-orange);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.6rem 0.75rem 0.75rem 0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 1.5rem;
} 

.btn-main-offer:hover{
  text-decoration: none;
  background-color: #f9ab00;
  background-color: var(--primary-orange);
  transition: .2s ease-in-out;
}

